<template>
    <div >
        <tou/>
        <div id="shouhuo" v-if="bSys">
            <div v-for="(item,k) in list" :key="k">
                <p>{{item.address}}</p>
                <p>{{item.phone}}</p>
                <button @click="fAA(item.id)">删除</button>
            </div>
        </div>
        <button id="but" v-if="bSys" @click="faA()">新增收货地址</button>
        <div v-if="bSys==false" id="input">
            <input type='text' placeholder="请填写你的名字" v-model="txt1"/>
            <input type='text' :placeholder="obj" @click="faC()" />
            <input type='text' placeholder="请写详情送餐地址" v-model="txt4"/>
            <input type='text' placeholder="请写能够联系到你的手机号" v-model="txt2"/>
            <input type='text' placeholder="备用联系电话（选填）"/>
            <input type="button" value="新增地址" style="background:red;border:0px" @click="faB()"/>
        </div>
    </div>
</template>
<script>
import tou from '../zhuye/toubiao'
import jQery from 'jquery'
export default {
    components:{
        tou,

    },
    data(){
        return{
            bSys:true,
            txt1:'',
            txt2:'',
            // txt3:'',
            txt4:'',
            list:[],
            obj:'请输入小区/学校'
        }
    },
    methods:{
        fAA(id){
             jQery.get(`http://elm.cangdu.org/v1/users/37962/addresses/`+id).then((e)=>{
                 console.log(e)
             })
        },
       faA(){
           this.bSys=false;
           console.log(document.getElementsByClassName('chang'))
           if(document.getElementsByClassName('chang')){
               var x=document.getElementsByClassName('chang');
               for(var i in  x){
                   console.log(x[i])
                //   x[i].style.display='none' 
               }
           }
       },
       faB(){

        if(this.txt1!==''&&this.txt2!==''){
            console.log(11)
           this.axios.post(`http://elm.cangdu.org/v1/users/37962/addresses`,{
               address: "11111111",
address_detail: "壹健身(珠江店)",
geohash: "31.22299,121.36025",
name: "李明娟",
phone: "17610654166",
phone_bk: "",
poi_type: 0,
sex: 1,
tag: "公司",
tag_type: 4
           }).then((e)=>{
               console.log(e)
            this.bSys=true;

           })
        
        }
       },
       faC(){
           this.$router.push('./shouhuozi')
       }
    },
    beforeMount(){
        localStorage.zhu='收货地址'
        this.axios.get(`http://elm.cangdu.org/v1/users/37962/addresses`).then((e)=>{
           this.list=e.data
           console.log(e.data)
       })
    },
    mounted(){
       if(this.$route.query.obj){
        this.obj=this.$route.query.obj.name
       }
    }

}
</script>
<style  scoped>
#chang{
    width: 90%;
    height: 100%;
    background: blue;
}
#shouhuo{
    width: 100%;
    height: auto;
}
#input>input{
    width: 90%;
    height: 1rem;
    margin-left: 0.4rem;
    margin-top: 0.2rem;
    border: 1px solid #999;
    background:#ccc;
}
#input{
    width: 100%;
    height: 100%;
}
#but{
    width: 100%;
    height: 1rem;
    border: 0px;
}
</style>


